import React from 'react'
import iconClose from '../assets/img/icon-close.png'

function dong(price) {
  return parseInt(price, 10).toLocaleString('vi-VI', {
    style: 'currency',
    currency: 'VND'
  })
}

export default function({ product, onRequestClose }) {
  if (product == null) return null

  return (
    <div>
      {onRequestClose != null ? (
        <div className="absolute top-1 right-1">
          <button
            className="bn bg-transparent outline-transparent"
            onClick={onRequestClose}>
            <img src={iconClose} alt="" />
          </button>
        </div>
      ) : null}
      <div className="mv5-l grid grid-11-l grid-col-gap1">
        <div className="grid-col-start-2-l grid-col-span-5-l">
          <div
            className="cover bg-center"
            style={{
              height: '28em',
              backgroundImage: `url(${product.image})`
            }}
          />

          <h3 className="f3 f-serif fw4">Thit k tng t</h3>
          <div className="flex item-center justify-center">
            <div
              className="w-33-l h5 cover bg-center mr3-l"
              style={{
                backgroundImage:
                  'url(https://res.cloudinary.com/duqeezi8j/image/upload/v1529417963/ej/flower-4.jpg)'
              }}
            />
            <div
              className="w-33-l h5 cover bg-center"
              style={{
                backgroundImage:
                  'url(https://res.cloudinary.com/duqeezi8j/image/upload/v1529417963/ej/flower-5.jpg)'
              }}
            />
            <div
              className="w-33-l h5 cover bg-center ml3-l"
              style={{
                backgroundImage:
                  'url(https://res.cloudinary.com/duqeezi8j/image/upload/v1529417963/ej/flower-6.jpg)'
              }}
            />
          </div>
        </div>
        <div className="grid-col-start-8-l grid-col-span-3-l lh-dbl dark-gray fw3">
          <h1 className="ttu f3 f-serif fw4">{product.name}</h1>
          {product.description > 0 ? <p>{product.description}</p> : null}

          <dl className="flex flex-column flex-row-l flex-wrap">
            <dt className="pt3 bb b--silver w-100 w-40-l">Kch thc:</dt>
            <dd className="pt3 bb b--silver w-100 w-60-l ml0">
              {product.size}
            </dd>
            <dt className="pt3 bb b--silver w-100 w-40-l">Gi tham kho:</dt>
            <dd className="pt3 bb b--silver w-100 w-60-l ml0">
              {dong(product.price)}
            </dd>
            <dt className="pt3 bb b--silver w-100 w-40-l">Cm xc:</dt>
            <dd className="pt3 bb b--silver w-100 w-60-l ml0">
              Tinh t, Hin i, Sang trng
            </dd>
          </dl>

          <p className="mt4">
            Mi mt sn phm ca ELJULIO thit k u mang mt v ring v ty
            bin theo cm xc ca khch hng, cng nh nguyn liu theo ma. V
            vy hnh nh v gi ch mang tnh cht hnh dung. Chng ti khng
            th thc hin mt sn phm ging 100% vi sn phm tham kho. Hy 
            chng ti mang n cho bn s bt ng.
          </p>

          <p className="i mt4">
            The photograph is good at the time of publication. Because there are
            individual differences, event if you request exact the same thing,
            we may not be able to prepare.
          </p>
        </div>
      </div>
    </div>
  )
}



