import React from 'react'
import { Helmet } from 'react-helmet'
import { withRouteData } from 'react-static'

import flowers from './flowers'
import { MENU_STUDIO } from '../constants'

import PageHeader from '../components/PageHeader'
import Layout from '../components/Layout'
import FlowerGrid from '../components/FlowerGrid'

function Header(props) {
  return (
    <div className="grid-col-span-9-l mb3">
      <PageHeader>{props.pageHeader}</PageHeader>

      <div className="mw8-ns center mt4-l">
        <p className="lh-copy dark-gray mt0 mb2 ph2 ph4-l f5 fw3">
          ELJULIO STUDIO l mt khng gian sng to hoa ngh thut theo yu cu
          ca tng c nhn hoc tng doanh nghip. Chng ti lm vic nh mt
          agency sng to, nhn "brief", cng "brainstorm" v sng to ra nhng
           tng kh thi nhm mang n mt tri nghim thng thc hoa ngh
          thut c o v duy nht cho ngi thng hoc cho khch hng ca
          bn. Cng chng ti to ra nhng khonh khc ng nh v trong mi
          chng ta u l nhng "bng hoa" ngt hng v sng ng.
        </p>
      </div>
    </div>
  )
}

function Content() {
  const items = flowers.map((image, id) => ({ id, image }))

  return <FlowerGrid items={items} />
}

export default withRouteData(function(props) {
  return (
    <div className="ph3-ns">
      <Helmet>
        <title>{props.pageHeader} :: El Julio</title>
      </Helmet>
      <Layout header={<Header {...props} />} submenu={MENU_STUDIO}>
        <Content />
      </Layout>
    </div>
  )
})



